import React from "react";
import { useEffect, useState, useContext } from "react";
import { CrudContext } from "../../../context/CrudContext";
import { registrarEntrada } from "../../../data/dataTable";
import { entradaSalidaValues } from "../../../data/initalValues";
import Tabla from "../../tabla/Tabla";
import { Select, Modal, Button, Input, Form } from "antd";
import { notificacion } from "../../../helpers/mensajes";
import ModalRegistrarProducto from "./ModalRegistrarProducto";
import dayjs from "dayjs";
import { AiOutlineForm } from "react-icons/ai";
import "../styles/modalRegistrarEntrada.css";

const ModalRegistrarSalida = ({
  almacen_id,
  actualizarTabla,
  productos,
  actualizarProductos,
  modal2,
  setModal2,
}) => {
  const [form] = Form.useForm();

  const {
    getData,
    dataToEdit,
    setDataToEdit,
    tipo,
    setCargando,
    data1,
    setData1,
    cargando,
    updateData,
    createData,
  } = useContext(CrudContext);

  const initialValues = entradaSalidaValues(tipo, almacen_id);
  const [salida, setSalida] = useState(initialValues);
  const [newJson, setNewJson] = useState([]);
  const [area, setArea] = useState([]);
  const [entradaId, setEntradaId] = useState([]);
  const [idCantidad, setIdCantidad] = useState("");
  const [trabajador, setTrabajador] = useState([]);
  const [pedido, setPedido] = useState([]);
  const [pedidoEntrada, setPedidoEntrada] = useState([]);
  const [productoAlmacen, setProductoAlmacen] = useState([]);
  const [buscador, setBuscador] = useState("");
  const [requerimiento, setRequerimiento] = useState([]);
  const [registrarProducto, setRegistrarProducto] = useState(false);

  const closeModal = () => {
    setModal2(false);
    setDataToEdit(null);
    setSalida(entradaSalidaValues);
    setNewJson([]);
    setCargando(false);
  };
  const getArea = async () => {
    const route = "area";
    const route1 = `entrada`;
    const route2 = "requerimiento/trabajador";
    const route3 = "pedido/id";
    const route4 = "pedido/entrada";
    const route5 = "requerimiento";

    const response = getData(route);
    const response1 = getData(route1);
    const response2 = getData(route2);
    const response3 = getData(route3);
    const response4 = getData(route4);
    const response5 = getData(`almacen/producto/${almacen_id}`);
    const response6 = getData(route5);

    const all = await Promise.all([
      response,
      response1,
      response2,
      response3,
      response4,
      response5,
      response6,
    ]);

    setArea(all[0].data);
    setEntradaId(all[1].data);
    setTrabajador(all[2].data);
    setPedido(all[3].data);
    setPedidoEntrada(all[4].data);
    setProductoAlmacen(all[5].data);
    setRequerimiento(all[6].data);
  };

  useEffect(() => {
    getArea();
  }, []);

  useEffect(() => {
    if (salida?.dni?.length > 7) {
      const filterDni = trabajador?.filter((item) => item.dni === salida.dni);

      if (filterDni.length > 0) {
        const traba = filterDni?.at(-1);
        setSalida((values) => ({
          ...values,
          encargado: traba?.nombre,
          area_id: traba.contrato.length > 0 && traba?.area?.id,
        }));
        form.setFieldsValue({
          encargado: traba?.nombre,
          area_id: traba.contrato.length > 0 && traba?.area?.id,
        });
      }
    }
  }, [salida.dni, trabajador]);
  //para calcular el costo total de de los productos
  useEffect(() => {
    let costo =
      newJson.length >= 0 &&
      newJson?.reduce(
        (acc, value) => parseFloat(acc) + parseFloat(value.costo),
        0
      );
    setSalida((value) => ({ ...value, costo_total: costo }));
  }, [newJson]);
  // para juntar productos iguales de requerimientos en uno sola salida
  useEffect(() => {
    if (
      dataToEdit === null &&
      salida.codigo_requerimiento &&
      salida.producto === ""
    ) {
      const filterRequerimiento = requerimiento.filter(
        (item) => item.id === salida.codigo_requerimiento
      );

      setSalida((value) => ({
        ...value,
        encargado: filterRequerimiento?.at(-1)?.solicitante,
        dni: filterRequerimiento?.at(-1)?.dni,
        area_id: filterRequerimiento?.at(-1)?.area_id,
        codigo_pedido: filterRequerimiento?.at(-1)?.pedido_id,
      }));
      form.setFieldsValue({
        encargado: filterRequerimiento?.at(-1)?.solicitante,
        dni: filterRequerimiento?.at(-1)?.dni,
        area_id: filterRequerimiento?.at(-1)?.area_id,
      });

      const formatProducto = filterRequerimiento
        ?.at(-1)
        ?.requerimiento_productos?.map((item) => {
          return {
            id: item?.producto_id,
            cantidad: item?.cantidad,
            nombre: item?.producto?.nombre,
            unidad: item?.unidad?.nombre,
            stock: item?.producto?.stock,
            costo: parseInt(item?.cantidad) * parseInt(item?.producto?.precio),
            producto_id: item.producto_id,
            almacen_id: almacen_id,
            codigo: "",
          };
        });
      if (formatProducto?.length > 0) {
        setNewJson(formatProducto);
      }
    }
  }, [dataToEdit, salida.codigo_requerimiento, salida.producto, form]);

  //agregar productos al usar el buscador
  useEffect(() => {
    if (salida.producto) {
      const filterProducto = data1?.filter(
        (item) => item.id === salida.producto
      );

      const format = filterProducto.map((item) => {
        return {
          id: item?.id,
          codigo_producto: item?.id,
          nombre: item?.nombre,
          unidad: item?.unidad,
          codigo: parseInt(entradaId[entradaId?.length - 1]?.id) + 1,
          producto_id: item?.id,
          tipo: "entrada",
          almacen_id: almacen_id,
          stock: parseInt(item?.stock),
          nuevo_stock: parseInt(item?.stock) + parseInt(salida?.cantidad),
          costo_inicial: parseFloat(item?.precio),
          costo: parseFloat(item?.precio)?.toFixed(2),
        };
      });
      const result = newJson?.filter((item) => {
        const prueba = format.find(
          (ele) => ele.codigo_producto === item.codigo_producto
        );

        if (prueba) {
          return item;
        } else {
          return prueba;
        }
      });

      if (result.length === 0) {
        setNewJson((value) => [...value, format.at(-1)]);
      }
    }
  }, [salida]);

  //para actualizar la cantidad y el costo en la tabla
  useEffect(() => {
    if (idCantidad !== "" && newJson.length !== 0 && idCantidad !== undefined) {
      setNewJson((state) =>
        state.map((item, i) =>
          i === idCantidad
            ? {
                ...item,
                cantidad: salida.cantidad,
                costo:
                  salida.cantidad !== ""
                    ? parseFloat(item.costo_inicial) *
                      parseFloat(salida.cantidad)
                    : item.costo_inicial,
              }
            : item
        )
      );
    }
  }, [idCantidad, salida.cantidad]);

  // data para editar
  useEffect(() => {
    if (dataToEdit) {
      setSalida(dataToEdit);
      form.setFieldsValue(dataToEdit);

      const formatData = dataToEdit?.producto_entrada_salidas?.map((item) => {
        return {
          cantidad: parseInt(item?.cantidad),
          costo: item?.costo,
          nombre: item?.producto?.nombre,
          unidad: item?.producto?.unidad?.nombre,
          id: item?.producto?.id,
          codigo_producto: item?.producto?.id,
          codigo: item?.producto?.codigo,
          producto_id: item?.producto?.id,
          stock: item?.producto?.stock,
        };
      });

      setNewJson(formatData);
    }
  }, [dataToEdit]);

  const handleData = (e, i, text) => {
    if (i !== undefined) {
      setIdCantidad(i);
    }
    if (!text && e.target) {
      const { name, value } = e.target;
      form.setFieldsValue({
        [name]: value,
      });
      setSalida((values) => {
        return { ...values, [name]: value };
      });
    } else {
      form.setFieldsValue({
        [text]: e,
      });
      setSalida((values) => {
        return { ...values, [text]: e };
      });
    }
  };

  const handleSubmit = async (e) => {
    let route = "entrada/salida";

    const objSalida = {
      codigo: salida.codigo,
      retornable: salida.retornable,
      motivo: salida.motivo,
      fecha: salida.fecha,
      dni: salida.dni,
      encargado: salida.encargado,
      area_id: salida.area_id,
      codigo_requerimiento: salida.codigo_requerimiento,
      costo_total: salida.costo_total,
      almacen_id: almacen_id,
      codigo_pedido: salida.codigo_pedido,
      productos: newJson,
    };

    const validarNegativos = newJson?.filter((item) => item.cantidad <= 0);
    const totalCantidad = newJson?.filter(
      (item) => item.cantidad !== ""
    ).length;
    const productosCantidad = newJson.length;
    const validarStock = newJson.filter(
      (item) => parseInt(item.stock) < parseInt(item.cantidad)
    );
    if (totalCantidad === productosCantidad) {
      if (validarNegativos.length === 0) {
        if (validarStock.length > 0) {
          return notificacion(
            500,
            "No se puede realizar la salida, stock insuficiente."
          );
        } else {
          if (dataToEdit === null) {
            setCargando(true);
            const response = await createData(objSalida, route);
            if (response) {
              notificacion(response.status, response.msg);
              closeModal();
              actualizarTabla();
              setCargando(false);
              actualizarProductos();
              getArea();
            }
          } else {
            setCargando(true);
            const response = await updateData(objSalida, dataToEdit.id, route);
            if (response) {
              notificacion(response.status, response.msg);
              closeModal();
              actualizarTabla();
              setCargando(false);
              actualizarProductos();
              getArea();
            }
          }
        }
      } else {
        return notificacion(500, "La cantidad debe ser mayor a 0.");
      }
    } else {
      return notificacion(
        500,
        "Debe completar el campo cantidad, para todos los productos seleccionados."
      );
    }
  };

  const handleDelete = (e) => {
    setNewJson((current) => current.filter((item) => item.id !== e.id));
    setSalida((value) => ({
      ...value,
      producto: "",
      codigo_pedido: "",
      codigo_requerimiento: "",
    }));
  };

  const columns = registrarEntrada(
    handleData,
    handleDelete,
    salida.cantidad,
    dataToEdit,
    salida?.codigo_pedido,
    newJson?.cantidad,
    salida.codigo_requerimiento,
    tipo
  );

  return (
    <>
      <Modal
        destroyOnClose={true}
        className="modal-registrar-entrada"
        title={dataToEdit ? `Editar salida` : `Registrar salida`}
        open={modal2}
        centered
        onCancel={closeModal}
        footer={null}
        width={950}
      >
        <Form form={form} onFinish={handleSubmit}>
          <section className="modal-body">
            <Form.Item className="container">
              <label>Código</label>
              <Input
                value={
                  dataToEdit === null
                    ? parseInt(entradaId.at(-1)?.id) + 1 || 1
                    : salida.id
                }
                type="text"
                name="codigo"
                disabled
                onChange={handleData}
              />
            </Form.Item>
            <Form.Item
              className="container"
              name="retornable"
              rules={[{ required: true, message: "Campo obligatorio!" }]}
            >
              <label>Retornable</label>
              <Select
                disabled={dataToEdit ? true : false}
                placeholder="Retornable"
                name="retornable"
                value={salida.retornable ? salida.retornable : undefined}
                onChange={(e) => handleData(e, null, "retornable")}
                options={[
                  {
                    label: "Si",
                    value: true,
                  },
                  {
                    label: "No",
                    value: false,
                  },
                ]}
              />
            </Form.Item>
            <Form.Item
              className="container"
              name="motivo"
              rules={[{ required: true, message: "Campo obligatorio!" }]}
            >
              <label>Motivo de salida</label>

              <Input
                disabled={dataToEdit ? true : false}
                placeholder={`Motivo de salida`}
                value={salida.motivo}
                type="text"
                name="motivo"
                onChange={handleData}
              />
            </Form.Item>
            <Form.Item
              className="container"
              name="fecha"
              rules={[{ required: true, message: "Campo obligatorio!" }]}
            >
              <label>Fecha de salida</label>
              <Input
                disabled={dataToEdit ? true : false}
                type="date"
                placeholder="Fecha "
                className="fecha"
                name="fecha"
                value={salida.fecha}
                onChange={handleData}
              />
            </Form.Item>

            <Form.Item
              className="container"
              name="dni"
              rules={[
                { required: true, message: "Campo obligatorio!" },
                {
                  min: 8,
                  message: "El dni tiene menos de 8 dígitos.",
                },
                {
                  max: 8,
                  message: "El dni tiene más de 8 dígitos.",
                },
              ]}
            >
              <label>Dni</label>
              <Input
                disabled={dataToEdit ? true : false}
                placeholder="Dni"
                type="number"
                name="dni"
                value={salida.dni}
                onChange={handleData}
              />
            </Form.Item>

            <Form.Item
              className="container"
              name="encargado"
              rules={[{ required: true, message: "Campo obligatorio!" }]}
            >
              <label>Personal</label>
              <Input
                disabled={dataToEdit ? true : false}
                placeholder="Personal"
                type="text"
                name="encargado"
                value={salida.encargado}
                onChange={handleData}
              />
            </Form.Item>
            <Form.Item
              className="container"
              name="area_id"
              rules={[{ required: true, message: "Campo obligatorio!" }]}
            >
              <label>Área</label>
              <Select
                disabled={dataToEdit ? true : false}
                placeholder="Área"
                name="area_id"
                dropdownMatchSelectWidth={false}
                value={salida.area_id? salida.area_id: undefined}
                onChange={(e) => handleData(e, null, "area_id")}
                showSearch
                optionFilterProp="children"
                filterOption={(input, option) =>
                  (option?.label ?? "")
                    .toLowerCase()
                    .includes(input.toLowerCase())
                }
                options={area.map((item, i) => {
                  return {
                    label: item.nombre,
                    value: item.id,
                  };
                })}
              />
            </Form.Item>

            <Form.Item className="container">
              <label>Código de requerimiento</label>

              <Select
                disabled={dataToEdit ? true : false}
                placeholder="Código de requerimiento"
                name="codigo_requerimiento"
                value={
                  salida.codigo_requerimiento ? salida.codigo_requerimiento : undefined
                }
                onChange={(e) => handleData(e, null, "codigo_requerimiento")}
              >
                <Select.Option value="-1">Ninguno</Select.Option>
                {requerimiento
                  ?.filter(
                    (item) =>
                      item.almacen_id === almacen_id &&
                      item.estado === "En almacén"
                  )
                  .map((item, i) => (
                    <Select.Option value={item.id}>{item.id}</Select.Option>
                  ))}
              </Select>
            </Form.Item>

            <Form.Item className="container">
              <label>Productos</label>

              <Select
                placeholder="Ingrese un producto"
                showSearch
                optionFilterProp="children"
                name="producto"
                disabled={
                  salida.codigo_requerimiento !== "" ||
                  (salida.codigo_pedido !== "" && salida.codigo_pedido !== "-1")
                    ? true
                    : false
                }
                // value={buscador}
                onSearch={(e) => setBuscador(e)}
                filterOption={(input, option) =>
                  (option?.label ?? "")
                    .toLowerCase()
                    .includes(input.toLowerCase())
                }
                onChange={(e) => handleData(e, null, "producto")}
                options={productos.map((item, i) => {
                  return {
                    value: item.id,
                    label: item.nombre,
                  };
                })}
              />
            </Form.Item>
          </section>

          <div className="tabla_salida">
            <Tabla columns={columns} table={newJson} filas={5} />
          </div>

          {newJson.length > 0 ? (
            <div className="footer">
              <label htmlFor="">
                <strong>Costo total:</strong> {salida.costo_total}
              </label>

              {newJson && newJson?.length !== 0 && !dataToEdit ? (
                <Button
                  htmlType="submit"
                  icon={<AiOutlineForm />}
                  loading={cargando ? true : false}
                >
                  Guardar
                </Button>
              ) : (
                ""
              )}
            </div>
          ) : (
            ""
          )}
        </Form>
      </Modal>

      {registrarProducto && (
        <ModalRegistrarProducto
          id={almacen_id}
          modal={registrarProducto}
          setModal={setRegistrarProducto}
          actualizarTabla={getArea}
        />
      )}
    </>
  );
};

export default ModalRegistrarSalida;
