import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import { useContext } from "react";
import { CrudContext } from "../../../context/CrudContext";
import { registrarEntrada } from "../../../data/dataTable";
import { entradaSalidaValues } from "../../../data/initalValues";
import Tabla from "../../tabla/Tabla";
import { Select, Modal, Button, Input, Form } from "antd";
import { notificacion } from "../../../helpers/mensajes";
import ModalRegistrarProducto from "./ModalRegistrarProducto";
import dayjs from "dayjs";
import { AiOutlineForm } from "react-icons/ai";
import "../styles/modalRegistrarEntrada.css";

const ModalRegistraEntrada = ({
  almacen_id,
  actualizarTabla,
  productos,
  actualizarProductos,
  modal2,
  setModal2,
}) => {
  const [form] = Form.useForm();

  const {
    getData,
    dataToEdit,

    setDataToEdit,
    tipo,
    setCargando,
    data1,
    setData1,
    cargando,
    updateData,
    createData,
  } = useContext(CrudContext);
  const initialValues = entradaSalidaValues(tipo, almacen_id);
  const [entrada, setEntrada] = useState(initialValues);
  const [newJson, setNewJson] = useState([]);
  const [area, setArea] = useState([]);
  const [entradaId, setEntradaId] = useState([]);
  const [idCantidad, setIdCantidad] = useState("");
  const [trabajador, setTrabajador] = useState([]);
  const [pedido, setPedido] = useState([]);
  const [pedidoEntrada, setPedidoEntrada] = useState([]);
  const [productoAlmacen, setProductoAlmacen] = useState([]);
  const [pedidoAlmacen, setPedidoAlmacen] = useState([]);
  const [buscador, setBuscador] = useState("");
  const [requerimiento, setRequerimiento] = useState([]);
  const [registrarProducto, setRegistrarProducto] = useState(false);
  const closeModal = () => {
    setModal2(false);
    setDataToEdit(null);
    setEntrada(initialValues);
    setNewJson([]);
    setCargando(false);
  };

  const getArea = async () => {
    const route = "area";
    const route1 = `entrada`;
    const route2 = "requerimiento/trabajador";
    const route3 = "pedido/id";
    const route4 = "pedido/entrada";
    const route5 = "requerimiento";

    const response = getData(route);
    const response1 = getData(route1);
    const response2 = getData(route2);
    const response3 = getData(route3);
    const response4 = getData(route4);
    const response5 = getData(`almacen/producto/${almacen_id}`);
    const response6 = getData(route5);

    const all = await Promise.all([
      response,
      response1,
      response2,
      response3,
      response4,
      response5,
      response6,
    ]);

    setArea(all[0].data);
    setEntradaId(all[1].data);
    setTrabajador(all[2].data);
    setPedido(all[3].data);
    setPedidoEntrada(all[4].data);
    setProductoAlmacen(all[5].data);
    setRequerimiento(all[6].data);
  };
  const getProductoAlmacen = async () => {
    const response = await getData(`almacen/producto/${almacen_id}`);
    setData1(response.data);
  };

  useEffect(() => {
    getProductoAlmacen();
  }, []);

  useEffect(() => {
    getArea();
  }, []);

  // para obtener al trabajador en base al dni
  useEffect(() => {
    if (entrada?.dni?.length > 7) {
      const filterDni = trabajador?.filter((item) => item.dni === entrada.dni);

      if (filterDni.length > 0) {
        const traba = filterDni?.at(-1);
        setEntrada((values) => ({
          ...values,
          encargado: traba?.nombre,
        }));

        form.setFieldsValue({
          encargado: traba?.nombre,
        });
      }
    }
  }, [entrada.dni, trabajador]);

  //para filtrar pedidos y requerimientos en base a su estado
  useEffect(() => {
    const filter = pedido
      .map((item) => {
        return {
          estado_pedido: item.estado_pedido,
          id: item.id,
          requerimiento_pedidos: item.requerimiento_pedidos.filter(
            (dat) => dat.almacen_id === almacen_id && dat.estado === null
          ),
        };
      })
      .flat()
      .filter((item) => item.requerimiento_pedidos.length > 0);

    setPedidoAlmacen(filter);
  }, [pedido, almacen_id]);
  // para juntar productos iguales de pedidos en uno solo entrada
  useEffect(() => {
    if (
      entrada.codigo_pedido !== "" &&
      entrada.codigo_pedido !== "-1" &&
      entrada.producto === "" &&
      dataToEdit === null
    ) {
      const filterData = pedidoEntrada.filter(
        (item) =>
          item.id === entrada.codigo_pedido &&
          item.requerimiento_pedidos
            .map((item) => item.requerimiento)
            .filter((item) => item.almacen_id === almacen_id)
      );

      const formatData =
        filterData.length > 0 &&
        filterData
          .map((item) =>
            item?.requerimiento_pedidos?.filter(
              (data) => data.requerimiento.almacen_id === almacen_id
            )
          )
          .flat()
          .map((item) => item.requerimiento_id)
          .toString();

      const formatProducto =
        filterData.length > 0 &&
        filterData
          .map((item) => item?.requerimiento_pedidos)
          .flat()
          .map((item) => item?.requerimiento?.requerimiento_productos)
          .flat()
          .map((item) => {
            return {
              cantidad: parseInt(item?.cantidad),
              almacen_id: item?.producto?.almacen_id,
              categoria: item?.producto?.categoria,
              id: item?.producto_id,
              codigo_barras: item?.producto?.codigo_barras,
              codigo_interno: item?.producto?.codigo_interno,
              descripcion: item?.producto?.descripcion,
              fecha: item?.producto?.fecha,
              producto_id: item?.producto_id,
              nombre: item?.producto?.nombre,
              precio: item?.producto?.precio,
              stock: item?.producto?.stock,
              unidad: item?.producto?.unidad?.nombre,
              encargado: filterData
                ?.map(
                  (data) =>
                    data?.requerimiento_pedidos?.at(-1)?.requerimiento
                      ?.solicitante
                )
                .flat()
                .toString(),
              dni: filterData
                ?.map(
                  (data) =>
                    data?.requerimiento_pedidos?.at(-1)?.requerimiento?.dni
                )
                .flat()
                .toString(),
            };
          });

      const mergeProducto =
        formatProducto.length > 0 &&
        formatProducto.reduce((value, current) => {
          let temp = value.find((o) => o.producto_id == current.producto_id);
          if (!temp) {
            value.push(current);
          } else {
            temp.cantidad += current.cantidad;
            Object.assign(temp);
          }
          return value;
        }, []);

      const formatMerge =
        mergeProducto.length > 0 &&
        mergeProducto
          .map((item) => {
            return {
              cantidad: item?.cantidad,
              almacen_id: item?.almacen_id,
              categoria: item?.categoria,
              id: item?.id,
              codigo_barras: item?.codigo_barras,
              codigo_interno: item?.codigo_interno,
              descripcion: item?.descripcion,
              fecha: entrada?.fecha,
              producto_id: item?.producto_id,
              nombre: item?.nombre,
              precio: item?.precio,
              stock: item?.stock,
              unidad: item?.unidad,
              costo:
                parseInt(item?.cantidad) * parseFloat(item?.precio).toFixed(2),
              tipo: entrada?.tipo,
              codigo: entrada?.codigo,
              motivo: entrada?.motivo,
              encargado: entrada?.encargado,
              codigo_compra: entrada?.codigo_compra,
              boleta: entrada?.boleta,
              codigo_requerimiento: formatData,
              dni: entrada.dni,
              codigo_pedido: entrada.codigo_pedido,
            };
          })
          .filter((item) => item.almacen_id === almacen_id);

      if (formatMerge.length > 0) {
        setEntrada((value) => ({ ...value, codigo_requerimiento: formatData }));
        setNewJson(formatMerge);
      }
    }
  }, [
    entrada.producto,
    entrada.fecha,
    entrada.tipo,
    entrada.codigo,
    entrada.motivo,
    entrada.encargado,
    entrada.codigo_compra,
    entrada.dni,
    entrada.codigo_pedido,
    entrada.costo_total,
    dataToEdit,
    entrada.boleta,
    pedidoEntrada,
    almacen_id,
    tipo,
  ]);
  // agregar productos a la tabla si se usa el buscador
  useEffect(() => {
    if (entrada.producto) {
      const filterProducto = data1?.filter(
        (item) => item.id === entrada.producto
      );

      const format = filterProducto.map((item) => {
        return {
          id: item?.id,
          codigo_producto: item?.id,
          nombre: item?.nombre,
          unidad: item?.unidad,
          codigo: parseInt(entradaId[entradaId?.length - 1]?.id) + 1,
          producto_id: item?.id,
          tipo: "entrada",
          almacen_id: almacen_id,
          stock: parseInt(item?.stock),
          nuevo_stock: parseInt(item?.stock) + parseInt(entrada?.cantidad),
          costo_inicial: parseFloat(item?.precio),
          costo: parseFloat(item?.precio)?.toFixed(2),
        };
      });
      const result = newJson?.filter((item) => {
        const prueba = format.find(
          (ele) => ele.codigo_producto === item.codigo_producto
        );

        if (prueba) {
          return item;
        } else {
          return prueba;
        }
      });

      if (result.length === 0) {
        setNewJson((value) => [...value, format.at(-1)]);
      }
    }
  }, [entrada]);

  // data para editar
  useEffect(() => {
    if (dataToEdit) {
      setEntrada(dataToEdit);
      form.setFieldsValue(dataToEdit);
      const formatData = dataToEdit?.producto_entrada_salidas?.map((item) => {
        return {
          cantidad: parseInt(item?.cantidad),
          costo: item?.producto?.precio,
          nombre: item?.producto?.nombre,
          unidad: item?.producto?.unidad?.nombre,
          id: item?.producto?.id,
          codigo_producto: item?.producto?.id,
          codigo: item?.producto?.codigo,
          producto_id: item?.producto?.id,
          stock: item?.producto?.stock,
        };
      });

      setNewJson(formatData);
    }
  }, [dataToEdit]);
  //para calcular el costo total de de los productos
  useEffect(() => {
    let costo =
      newJson.length >= 0 &&
      newJson?.reduce(
        (acc, value) => parseFloat(acc) + parseFloat(value.costo),
        0
      );
    setEntrada((value) => ({ ...value, costo_total: costo }));
  }, [newJson]);
  //para actualizar la cantidad y el costo en la tabla
  useEffect(() => {
    if (idCantidad !== "" && newJson.length !== 0 && idCantidad !== undefined) {
      setNewJson((state) =>
        state.map((item, i) =>
          i === idCantidad
            ? {
                ...item,
                cantidad: entrada.cantidad,
                costo:
                  entrada.cantidad !== ""
                    ? parseFloat(item.costo_inicial) *
                      parseFloat(entrada.cantidad)
                    : item.costo_inicial,
              }
            : item
        )
      );
    }
  }, [idCantidad, entrada.cantidad]);

  const handleData = (e, i, text) => {
    if (i !== undefined) {
      setIdCantidad(i);
    }

    if (!text && e.target) {
      const { name, value } = e.target;

      setEntrada((values) => {
        return { ...values, [name]: value };
      });
      form.setFieldsValue({
        [name]: value,
      });
    } else {
      setEntrada((values) => {
        return { ...values, [text]: e };
      });
      form.setFieldsValue({
        [text]: e,
      });
    }
  };

  const handleSubmit = async (e) => {
    let route = "entrada";

    const objEntrada = {
      motivo: entrada.motivo,
      fecha: entrada.fecha,
      dni: entrada.dni,
      encargado: entrada.encargado,
      codigo_compra: entrada.codigo_compra,
      boleta: entrada.boleta,
      codigo_pedido: entrada.codigo_pedido,
      codigo_requerimiento: entrada.codigo_requerimiento,
      almacen_id: almacen_id,
      productos: newJson,
      codigo: entradaId.at(-1)?.id + 1 || 1,
    };
    const validarNegativos = newJson?.filter((item) => item.cantidad <= 0);
    const totalCantidad = newJson?.filter(
      (item) => item.cantidad !== ""
    ).length;
    const productosCantidad = newJson.length;

    if (totalCantidad === productosCantidad) {
      if (validarNegativos.length === 0) {
        if (dataToEdit === null) {
          setCargando(true);
          const response = await createData(objEntrada, route);
          if (response) {
            notificacion(response.status, response.msg);
            closeModal();
            actualizarTabla();
            setCargando(false);
            actualizarProductos();
            getArea();
          }
        } else {
          setCargando(true);
          const response = await updateData(objEntrada, dataToEdit.id, route);
          if (response) {
            notificacion(response.status, response.msg);
            closeModal();
            actualizarTabla();
            setCargando(false);
            actualizarProductos();
            getArea();
          }
        }
      } else {
        notificacion(500, "La cantidad debe ser mayor a 0.");
      }
    } else {
      notificacion(
        500,
        "Debe completar el campo cantidad, para todos los productos seleccionados."
      );
    }
  };

  const handleDelete = (e) => {
    setNewJson((current) => current.filter((item) => item.id !== e.id));
    setEntrada((value) => ({
      ...value,
      producto: "",
      codigo_pedido: "",
      codigo_requerimiento: "",
    }));
  };

  const columns = registrarEntrada(
    handleData,
    handleDelete,
    entrada.cantidad,
    dataToEdit,
    entrada?.codigo_pedido,
    newJson?.cantidad,
    entrada.codigo_requerimiento,
    tipo
  );

  return (
    <>
      <Modal
        destroyOnClose={true}
        className="modal-registrar-entrada"
        title={dataToEdit ? `Editar entrada` : `Registrar entrada`}
        open={modal2}
        centered
        onCancel={closeModal}
        footer={null}
        width={950}
      >
        <Form form={form} onFinish={handleSubmit}>
          <section className="modal-body">
            <Form.Item className="container">
              <label>Código</label>
              <Input
                value={
                  dataToEdit === null
                    ? parseInt(entradaId.at(-1)?.id) + 1 || 1
                    : entrada.codigo
                }
                type="text"
                name="codigo"
                disabled
                onChange={handleData}
              />
            </Form.Item>
            <Form.Item
              className="container"
              name="motivo"
              rules={[{ required: true, message: "Campo obligatorio!" }]}
            >
              <label>Motivo de entrada *</label>

              <Input
                disabled={dataToEdit ? true : false}
                placeholder={`Motivo de entrada`}
                value={entrada.motivo}
                type="text"
                name="motivo"
                onChange={handleData}
              />
            </Form.Item>
            <Form.Item
              className="container"
              name="fecha"
              rules={[{ required: true, message: "Campo obligatorio!" }]}
            >
              <label>Fecha de entrada</label>
              <Input
                disabled={dataToEdit ? true : false}
                type="date"
                placeholder="Fecha "
                className="fecha"
                name="fecha"
                value={entrada.fecha}
                onChange={handleData}
              />
            </Form.Item>
            <Form.Item
              className="container"
              name="dni"
              rules={[
                { required: true, message: "Campo obligatorio!" },
                {
                  min: 8,
                  message: "El dni tiene menos de 8 dígitos.",
                },
                {
                  max: 8,
                  message: "El dni tiene más de 8 dígitos.",
                },
              ]}
            >
              <label>Dni</label>
              <Input
                disabled={dataToEdit ? true : false}
                placeholder="Dni"
                type="number"
                name="dni"
                value={entrada.dni}
                onChange={handleData}
              />
            </Form.Item>
            <Form.Item
              className="container"
              name="encargado"
              rules={[{ required: true, message: "Campo obligatorio!" }]}
            >
              <label>Encargado</label>
              <Input
                disabled={dataToEdit ? true : false}
                placeholder="Encargado"
                type="text"
                name="encargado"
                value={entrada.encargado}
                onChange={handleData}
              />
            </Form.Item>
            <Form.Item
              className="container"
              name="codigo_compra"
              rules={[{ required: false }]}
            >
              <label>Código orden de compra</label>
              <Input
                disabled={dataToEdit ? true : false}
                placeholder="Código orden de compra"
                type="text"
                name="codigo_compra"
                value={entrada.codigo_compra}
                onChange={handleData}
              />
            </Form.Item>
            <Form.Item
              className="container"
              name="boleta"
              rules={[{ required: false }]}
            >
              <label>Boleta/Factura</label>
              <Input
                disabled={dataToEdit ? true : false}
                placeholder="Boleta/Factura"
                type="text"
                name="boleta"
                value={entrada.boleta}
                onChange={handleData}
              />
            </Form.Item>
            <Form.Item className="container">
              <label>Código de pedido</label>
              <Select
                disabled={dataToEdit ? true : false}
                placeholder="Codigo de pedido"
                name="codigo_pedido"
                value={
                  entrada.codigo_pedido ? entrada.codigo_pedido : undefined
                }
                onChange={(e) => handleData(e, null, "codigo_pedido")}
              >
                <Select.Option value="-1">Ninguno</Select.Option>
                {pedidoAlmacen?.map((item, i) => (
                  <Select.Option value={item.id}>{item.id}</Select.Option>
                ))}
              </Select>
            </Form.Item>
            <Form.Item className="container">
              <label>Código de requerimiento</label>

              <Select
                disabled
                placeholder="Código de requerimiento"
                name="codigo_requerimiento"
                value={
                  entrada.codigo_requerimiento
                    ? entrada.codigo_requerimiento
                    : undefined
                }
                onChange={(e) => handleData(e, null, "codigo_requerimiento")}
              >
                <Select.Option value="-1">Ninguno</Select.Option>
                {requerimiento
                  ?.filter(
                    (item) =>
                      item.almacen_id === almacen_id &&
                      item.estado !== "Entregado"
                  )
                  .map((item, i) => (
                    <Select.Option value={item.id}>{item.id}</Select.Option>
                  ))}
              </Select>
            </Form.Item>
            <Form.Item className="container">
              <label>Productos</label>
              <Select
                placeholder="Seleccione un producto"
                showSearch
                optionFilterProp="children"
                name="producto"
                disabled={
                  entrada.codigo_requerimiento !== "" ||
                  (entrada.codigo_pedido !== "" &&
                    entrada.codigo_pedido !== "-1")
                    ? true
                    : false
                }
                value={entrada.producto ? entrada.producto : undefined}
                onSearch={(e) => setBuscador(e)}
                filterOption={(input, option) =>
                  (option?.label ?? "")
                    .toLowerCase()
                    .includes(input.toLowerCase())
                }
                onChange={(e) => handleData(e, null, "producto")}
                options={productos.map((item, i) => {
                  return {
                    value: item.id,
                    label: item.nombre,
                  };
                })}
              />
            </Form.Item>
            {!dataToEdit ? (
              <div className="agregar">
                <Button onClick={() => setRegistrarProducto(true)}>+</Button>
              </div>
            ) : (
              ""
            )}
          </section>

          <div className="tabla_salida">
            <Tabla columns={columns} table={newJson} filas={5} />
          </div>

          <div className="button-container">
            {newJson.length > 0 ? (
              <div className="footer">
                <label htmlFor="">
                  <strong>Costo total:</strong> {entrada.costo_total}
                </label>

                {newJson && newJson?.length !== 0 && !dataToEdit ? (
                  <Button
                    htmlType="submit"
                    icon={<AiOutlineForm />}
                    loading={cargando ? true : false}
                  >
                    Guardar
                  </Button>
                ) : (
                  ""
                )}
              </div>
            ) : (
              ""
            )}
          </div>
        </Form>
      </Modal>

      {registrarProducto && (
        <ModalRegistrarProducto
          id={almacen_id}
          modal={registrarProducto}
          setModal={setRegistrarProducto}
          actualizarTabla={getProductoAlmacen}
        />
      )}
    </>
  );
};

export default ModalRegistraEntrada;
