import { Modal } from "antd";
import React, { useContext, useEffect, useState } from "react";
import { CrudContext } from "../../../context/CrudContext";
import { productoSalida } from "../../../data/dataTable";
import { notificacion } from "../../../helpers/mensajes";
import Tabla from "../../tabla/Tabla";
import BuscadorEntradaSalida from "../BuscadorEntradaSalida";
import ModalRegistrarSalida from "./ModalRegistrarSalida";

const ModalSalida = ({ id, data, actualizarProductos, modal, setModal }) => {
  const { setModal2, tipo, setTipo, getData, setDataToEdit, createData } =
    useContext(CrudContext);
  const [historial, setHistorial] = useState([]);
  const [search, setSearch] = useState([]);
  const [filterText, setFilterText] = useState("");
  const [modalSalida, setModalSalida] = useState(false);
  const closeModal = () => {
    setModal(false);
    setTipo("");
  };
  const getHistorial = async () => {
    const routeId = `entrada/${id}?tipo=salida`;
    const response = await getData(routeId);
    setHistorial(response.data);
  };
  useEffect(() => {
    getHistorial();
  }, [tipo]);

  const handleEdit = (e) => {
    setDataToEdit(e);
    setModalSalida(true);
  };

  useEffect(() => {
    let filter = historial?.filter(
      (item) =>
        item?.id
          ?.toString()
          ?.toLowerCase()
          ?.includes(filterText?.toLowerCase()) ||
        item?.encargado?.toLowerCase()?.includes(filterText?.toLowerCase()) ||
        item?.codigo_compra
          ?.toLowerCase()
          ?.includes(filterText?.toLowerCase()) ||
        item?.codigo_factura
          ?.toLowerCase()
          ?.includes(filterText?.toLowerCase()) ||
        item?.area?.nombre
          ?.toLowerCase()
          ?.includes(filterText?.toLowerCase()) ||
        item?.motivo?.toLowerCase()?.includes(filterText?.toLowerCase())
    );

    if (filterText) {
      setSearch(filter);
    } else {
      setSearch(historial);
    }
  }, [filterText, historial, tipo]);

  const handleDelete = async (e) => {
    const route = `entrada/eliminar/${e.id}`;

    const response = await createData(e, route);
    if (response.status === 200) {
      notificacion(response.status, response.msg);
      getHistorial();
      actualizarProductos();
    }
  };

  const columns = productoSalida(handleEdit, handleDelete);
  return (
    <Modal
      className="modal-entradas"
      title={"Historial de salidas"}
      open={modal}
      centered
      onCancel={closeModal}
      footer={null}
      width={1020}
    >
      <BuscadorEntradaSalida
        abrirModal={setModalSalida}
        modal={true}
        filterText={setFilterText}
      />

      <br />

      <Tabla columns={columns} table={search} filas={8} />

      {modalSalida && (
        <ModalRegistrarSalida
          almacen_id={id}
          actualizarTabla={getHistorial}
          productos={data}
          tipo={tipo}
          actualizarProductos={actualizarProductos}
          modal2={modalSalida}
          setModal2={setModalSalida}
        />
      )}
    </Modal>
  );
};

export default ModalSalida;
