import { Modal } from "antd";
import React, { useContext, useEffect, useState } from "react";
import { CrudContext } from "../../../context/CrudContext";
import { productoEntrada } from "../../../data/dataTable";
import { notificacion } from "../../../helpers/mensajes";
import Tabla from "../../tabla/Tabla";
import BuscadorEntradaSalida from "../BuscadorEntradaSalida";
import ModalRegistraEntrada from "./ModalRegistraEntrada";

const ModalEntradas = ({ modal, setModal, id, data, actualizarProductos }) => {
  const {
    setModal1,
    modal1,
    setModal2,
    tipo,
    setTipo,
    deleteData,
    getData,
    setDataToEdit,
    updateData,
    createData,
  } = useContext(CrudContext);
  const [historial, setHistorial] = useState([]);
  const [search, setSearch] = useState([]);
  const [modalEntradas, setModalEntradas] = useState(false);
  const [filterText, setFilterText] = useState("");

  const getHistorial = async () => {
    const routeId = `entrada/${id}?tipo=entrada`;
    const response = await getData(routeId);
    setHistorial(response.data);
  };

  useEffect(() => {
    getHistorial();
  }, [tipo]);

  const handleEdit = (e) => {
    setDataToEdit(e);
    setModalEntradas(true);
  };
  const closeModal = () => {
    setModal(false);
    setTipo("");
  };
  const handleDelete = async (e) => {
    const route = `entrada/eliminar/${e.id}`;

    const response = await createData(e, route);
    if (response.status === 200) {
      notificacion(response.status, response.msg);
      getHistorial();
      actualizarProductos();
    }
  };
  useEffect(() => {
    let filter = historial?.filter(
      (item) =>
        item?.id
          ?.toString()
          ?.toLowerCase()
          ?.includes(filterText?.toLowerCase()) ||
        item?.encargado?.toLowerCase()?.includes(filterText?.toLowerCase()) ||
        item?.codigo_compra
          ?.toLowerCase()
          ?.includes(filterText?.toLowerCase()) ||
        item?.codigo_factura
          ?.toLowerCase()
          ?.includes(filterText?.toLowerCase()) ||
        item?.fecha?.toLowerCase()?.includes(filterText?.toLowerCase())
    );

    if (filterText) {
      setSearch(filter);
    } else {
      setSearch(historial);
    }
  }, [filterText, historial, tipo]);

  const colums = productoEntrada(handleEdit, handleDelete);

  return (
    <Modal
      className="modal-entradas"
      title={"Historial de entradas"}
      open={modal}
      centered
      onCancel={closeModal}
      footer={null}
      width={1020}
    >
      <BuscadorEntradaSalida
        abrirModal={setModalEntradas}
        modal={true}
        filterText={setFilterText}
      />

      <br />

      <Tabla columns={colums} table={search} filas={8} />

      {modalEntradas && (
        <ModalRegistraEntrada
          almacen_id={id}
          actualizarTabla={getHistorial}
          productos={data}
          tipo={tipo}
          actualizarProductos={actualizarProductos}
          modal2={modalEntradas}
          setModal2={setModalEntradas}
        />
      )}
    </Modal>
  );
};

export default ModalEntradas;
